:root {
  --primary-color: #00baedff;
  --secondary-color: #143bb0d4;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
@media (max-width:600px){
  .container{
    width: 70%;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.iconButtonStyle{
  transition: all 100ms !important;
}
.TodoStyle{
  transition: all 0.2s !important;
}
.TodoStyle:hover{
  padding-top: 30px;
  padding-bottom: 35px !important;
  background-color: var(--secondary-color
  ) !important;
}

.iconButton:hover{
  background-color: rgba(175, 200, 215, 0.946) !important;
  box-shadow: 3px 4px 6px rgb(161, 160, 160) !important;
}

.iconButtonStyle:hover{
  transform: scale(1.5);
}

@font-face{
  src: url("../public/font/Oswald/Oswald-VariableFont_wght.ttf");
  font-family: newFont;
}
.addButton:hover{
  background-color: var(--secondary-color) !important;
}